import React, { Fragment } from "react";
import MetaTags from "react-meta-tags";
import Layout from "../../layouts/Layout";
import HeroSlider from "../../wrappers/hero-slider/HeroSlider";
import TabProduct from "../../wrappers/product/TabProduct";
import FeatureIcon from "../../wrappers/feature-icon/FeatureIcon";
import Promo from "../../wrappers/promos/Promos";
import { multilanguage } from "redux-multilanguage";
import { connect } from "react-redux";

const Home = ({ merchant, strings }) => {
  return (
    <Fragment>
      <MetaTags>
        <title>{merchant.name}</title>
        { <meta
          name="description"
          content="Vente et installation de poles et foyers au gaz et granules. BBQ et accessoires. Perfecto Gaz, votre partenaire de confiance pour tous vos besoins en chauffage au gaz propane"

        /> }
      </MetaTags>
      <Layout
        headerContainerClass="container-fluid"
        headerPaddingClass="header-padding-2"
        headerTop="visible"
      >
        {/* hero slider */}
        <HeroSlider string={strings} />
        {/* feature icon */}
        <FeatureIcon
        bgImg="/assets/img/bg/shape.png"
        containerClass="container-fluid"
        gutterClass="padding-10-row-col"
        spaceTopClass="pt-20"
        spaceBottomClass="pb-20"
      />
        { /*promos */}
        <Promo
          bgImg=""
          containerClass="container-fluid"
          gutterClass="padding-10-row-col"
          /*spaceTopClass="pt-50"*/
          spaceBottomClass="pb-40"
        />

        <div className="container pt-20 pb-50" >
          <p className="content-size">
          Bienvenue chez <b>Perfecto Gaz</b>, votre partenaire de confiance pour tous vos besoins en chauffage au gaz propane. 
          </p>
          <p className="content-size">
          Depuis 2000, nous nous engageons  fournir des solutions de chauffage fiables, efficaces et respectueuses de l'environnement pour les rsidences et les entreprises. Que vous envisagiez de remplacer votre systme de chauffage existant ou d'installer un nouveau systme, notre quipe exprimente est l pour vous guider  chaque tape du processus.
          </p>
          <p className="content-size">
          Nous offrons une gamme complte d'appareils de chauffage au gaz, soigneusement slectionns parmi les meilleures marques de l'industrie, pour rpondre  vos besoins spcifiques en matire de confort et d'efficacit nergtique.
          </p>
          <p className="content-size">
          Notre service d'installation est effectu par des techniciens qualifis et certifis, garantissant une installation prcise et conforme aux normes de scurit les plus strictes. De plus, nous proposons des services d'entretien rgulier pour assurer le bon fonctionnement et la longvit de votre systme de chauffage.
          </p><p className="content-size">
          Chez <b>Perfecto Gaz</b>, nous sommes fiers de fournir  nos clients une exprience personnalise et un service clientle exceptionnel. Notre objectif est de vous offrir la tranquillit d'esprit en sachant que votre systme de chauffage est entre de bonnes mains.
          &nbsp;<a href="/contact">Contactez-nous</a> ds aujourd'hui pour en savoir plus sur nos produits et services de chauffage au gaz. Faites le choix de la qualit, de la fiabilit et du confort avec Perfecto Gaz.

        </p>
        </div>

        {/* tab product */}
        <TabProduct
          category="fashion"
          spaceBottomClass="pb-40"
          spaceTopClass="pt-40"
        />

        {/* newsletter */}
        {/**
        <Newsletter
          spaceTopClass="pt-100"
          spaceBottomClass="pb-100"
          subscribeBtnClass="dark-red-subscribe"
        />
        **/}
      </Layout>
    </Fragment>
  );
};


const mapStateToProps = state => {
  return {
    merchant: state.merchantData.merchant
  };
};


export default connect(mapStateToProps, null)(multilanguage(Home));
